<!--  -->
<!-- eslint-disable vue/no-v-html -->

<template>
  <div
    v-if="show"
    ref="componentContainer"
    class="cert-poster"
  >
    <img
      class="cert-poster__background"
      :src="certBackground"
    >
    <div class="cert-poster__title">
      {{ certTitle }}
    </div>
    <div class="cert-poster__name">
      {{ certGreet }}
    </div>
    <div
      class="cert-poster__content"
      v-html="certContent"
    />
    <div class="cert-poster__no">
      {{ certNumber }}
    </div>
    <div
      class="cert-poster__signature"
      v-html="certSignature"
    />
    <img
      class="cert-poster__stamp"
      :src="certStamp"
    >
  </div>
</template>

<script>
import dayjs from 'dayjs'
import html2canvas from 'html2canvas'

export default {
  name: 'CertPoster',

  props: {
    value: {
      type: Boolean,
      default: false
    },

    // 
    certTitle: {
      type: String,
      default: ''
    },

    // 
    certBackground: {
      type: String,
      default: ''
    },

    // 
    certStamp: {
      type: String,
      default: ''
    },

    // 
    certGreet: {
      type: String,
      default: ''
    },

    // 
    certContent: {
      type: String,
      default: ''
    },

    // 
    certSignature: {
      type: String,
      default: ''
    },

    // 
    certAwardName: {
      type: String,
      default: ''
    },

    // 
    certNumber: {
      type: String,
      default: ''
    },

    // 
    playerNumber: {
      type: Number,
      default: 0
    },

    // 
    playerName: {
      type: String,
      default: ''
    },

    // 
    matchName: {
      type: String,
      default: ''
    }
  },

  data() {
    return {
      show: this.value,
      date: new Date()
    }
  },

  watch: {
    value(newVal) {
      this.show = newVal

      if (newVal) {
        this.$nextTick(() => {
          this.createPoster()
        })
      }
    },

    certTitle: {
      handler: 'updateFieldValue',
      immediate: true
    },

    certBackground: {
      handler: 'updateFieldValue',
      immediate: true
    },

    certStamp: {
      handler: 'updateFieldValue',
      immediate: true
    },

    certGreet: {
      handler: 'updateFieldValue',
      immediate: true
    },

    certContent: {
      handler: 'updateFieldValue',
      immediate: true
    },

    certSignature: {
      handler: 'updateFieldValue',
      immediate: true
    },

    certAwardName: {
      handler: 'updateFieldValue',
      immediate: true
    },

    certNumber: {
      handler: 'updateFieldValue',
      immediate: true
    },

    playerNumber: {
      handler: 'updateFieldValue',
      immediate: true
    },

    playerName: {
      handler: 'updateFieldValue',
      immediate: true
    },

    matchName: {
      handler: 'updateFieldValue',
      immediate: true
    }
  },

  methods: {
    /**
    *  0
    * @param num 
    * @param n 
    */
    prefixZero(num, n) {
      return (Array(n).join(0) + num).slice(-n)
    },

    // 
    contentFormat(str) {
      // {}
      const list = [
        () => this.playerNumber ? str.replace(/{}/g, String(this.playerNumber).length < 6 ? this.prefixZero(this.playerNumber, 5) : this.playerNumber) : str, // 
        () => str.replace(/{YYYYMMDD}/g, dayjs(this.date).format('YYYYMMDD')), // 
        () => str.replace(/{YYYYMM}/g, dayjs(this.date).format('YYYYMM')), // 
        () => this.certAwardName ? str.replace(/{}/g, this.certAwardName) : str, // 
        () => this.playerName ? str.replace(/{}/g, this.playerName) : str, // 
        () => this.matchName ? str.replace(/{}/g, this.matchName) : str, // 
        () => str.replace(/\n/g, '<br>') //  HTML 
      ]

      list.forEach(elm => {
        str = elm()
      })

      return str
    },

    // 
    updateFieldValue() {
      this.certTitle = this.contentFormat(this.certTitle) // 
      this.certContent = this.contentFormat(this.certContent) // 
      this.certGreet = this.contentFormat(this.certGreet) // 
      this.certNumber = this.contentFormat(this.certNumber) // 
      this.certSignature = this.contentFormat(this.certSignature) // 
    },

    // 
    createPoster() {
      //  http://html2canvas.hertzen.com/configuration/
      const opts = {
        scale: 3, // 
        // logging: true, //  html2canvas 
        useCORS: true, // 
        allowTaint: true, // 
        backgroundColor: '#0000', // 
        scrollY: 0 //  0 bughttps://github.com/niklasvh/html2canvas/issues/1878
      }

      // HTML 
      html2canvas(this.$refs.componentContainer, opts)
        .then(canvas => {
          const base64 = canvas.toDataURL('image/jpeg') //  base64 

          this.$emit('success', base64)
          this.close()
        })
        .catch(() => {
          this.$emit('fail')
          this.close()
        })
    },

    close() {
      this.$emit('input', false)
    }
  }
}
</script>

<style>
.cert-poster {
  position: fixed;
  top: -9999999px;
  left: -9999999px;
  width: 600px;
  height: 460px;
  padding: 104px 60px 38px;
  color: #444244;
  font-size: 12px;
  font-weight: 400;
  box-sizing: border-box;
}

.cert-poster .cert-poster__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.cert-poster .cert-poster__title {
  font-size: 20px;
  font-weight: 800;
  text-align: center;
}

.cert-poster .cert-poster__name {
  font-size: 16px;
  color: #b48d2c;
  padding-top: 19px;
  padding-bottom: 10px;
  font-weight: 800;
  text-align: center;
}

.cert-poster .cert-poster__content {
  line-height: 20px;
  text-indent: 24px;
}

.cert-poster .cert-poster__no {
  position: absolute;
  bottom: 38px;
  left: 60px;
}

.cert-poster .cert-poster__signature {
  position: absolute;
  right: 60px;
  bottom: 38px;
  width: 192px;
  line-height: 20px;
  text-align: right;
}

.cert-poster .cert-poster__stamp {
  position: absolute;
  right: 60px;
  bottom: 29px;
  width: 88px;
  height: 88px;
}
</style>
